import React from "react";
import Header from "../commonComponent/header";
import SideBar from "../commonComponent/sideBar";
import { Link } from "react-router-dom";

function TransactionDetails() {
  return (
    <>
      <Header />

      <div className="admin_main">
        <SideBar />
        <div className="admin_contentpart">
          <Link to="/admin/transaction-management" className="comman_btn w-fit">
            <i className="fa fa-arrow-left" />
          </Link>
          <div className="row comman_design mx-0 mt-4">
            <div className="col-12">
              <div className="row comman_table pt-md-3 pb-md-4 pb-3 px-md-3 px-2">
                <div className="col-12">
                  <div>
                    <div className="rating-user-img-wrapper ">
                      <img
                        src="../../assets/img/user1.png"
                        alt=""
                        className="w-100 h-100"
                      />
                    </div>
                    <div className="w-100 mt-2">
                      <div>
                        <span className="fs-6 fw-bold mb-0">Jhon Doe</span>
                      </div>
                      <div className="mt-3">
                        <span className="fs-6 fw-bold mb-0">Date : </span>
                        <span className="fs-6 fw-light mb-0">10/10/2023</span>
                      </div>
                      <div className="mt-3">
                        <span className="fs-6 fw-bold mb-0">Amount : </span>
                        <span className="fs-6 fw-light mb-0">100 ED</span>
                      </div>
                      <div className="mt-3">
                        <span className="fs-6 fw-bold mb-0">
                          Payment Method :{" "}
                        </span>
                        <span className="fs-6 fw-light mb-0">Apple Pay</span>
                      </div>
                      <div className="mt-3">
                        <span className="fs-6 fw-bold mb-0">
                          Plan Purchased :{" "}
                        </span>
                        <span className="fs-6 fw-light mb-0">Basic</span>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}

export default TransactionDetails;
